import React from "react";

import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel as CarouselItem } from "react-responsive-carousel";

import "./Carousel.css";

function Carousel() {
  const carouselItems = [
    {
      imgSrc: "data:image/jpeg;base64,",
      title: "Mens",
      subtitle1: "Cloths",
      title2: "Collection",
      subtitle2: "New Trending Cloths",
      btnText: "Cloths Collection",
    },
    {
      imgSrc: "data:image/jpeg;base64,",
      title: "Huge",
      subtitle1: "Sale",
      title2: "40% Off",
      subtitle2: "Big Sale Cloths",
      btnText: "Cloths Collection",
    },
    {
      imgSrc: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTnT0GXr2nbQ6leL7Pa7_h0d1alvL_BpIVk4A&usqp=CAU",
      title: "Mens",
      subtitle1: "Cloths",
      title2: "Collection",
      subtitle2: "New Trending Cloths",
      btnText: "Cloths Collection",
    },
  ];
  return (
    <CarouselItem
      showThumbs={false}
      showStatus={false}
      infiniteLoop={true}
      // autoPlay={true}
    >
      {carouselItems.map((item, i) => {
        return (
          <div className="cr-container" key={i}>
            <img src={item.imgSrc} />
            <div className="cr-content">
              <h1>{item.title}</h1>
              <h3>{item.subtitle1}</h3>
              <h2>{item.title2}</h2>
              <h4>{item.subtitle2}</h4>
              <button>{item.btnText}</button>
            </div>
          </div>
        );
      })}
    </CarouselItem>
  );
}

export default Carousel;