import React from "react";

import Components from "../../components";
import items from "../../assets/items";
import "./Home.css";

function Home(props) {
  const productType = [
    {
      src: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSTm8NjpDP9SNFolvyWQOTXL1lvTo2b78RgUA&usqp=CAU",
      title: "Men's Cloth Collection",
    },
    {
      src: "data:image/jpeg;base64,",
      title: "Women's Cloth Collection",
    }
    
  ];

  const trustedPartners = [
    {
      img:"https://img.freepik.com/free-vector/orange-logo-arrow-shape_1043-53.jpg",
      altKey: "Arrow",
      name: "Arrow",
    },
    {
      img: "https://www.marketing91.com/wp-content/uploads/2019/05/SWOT-Analysis-of-Peter-England-3.jpg",
      altKey: "Peter England",
      name: "Peter England",
    },
    {
      img: "data:image/jpeg;base64,",
      altKey: "Park Avenue",
      name: "Park Avenue",
    },
    {
      img: "data:image/png;base64,",
      altKey: "Louis Philippe",
      name: "Louis Philippe",
    },
    {
      img: "https://upload.wikimedia.org/wikipedia/commons/a/ab/Zodiac_Logo.jpg",
      altKey: "Zodiac",
      name: "Zodiac",
    },
  ];

  const handleCartUpdate = (item, action) => {
    let updatedCart;
    if (action === "add") {
      updatedCart = [...props.cartItems, item];
    } else if (action === "remove") {
      updatedCart = props.cartItems.filter(
        (cartItem) => cartItem.id !== item.id
      );
    }
    props.updateCart(updatedCart);
  };

  return (
    <div>
      <Components.Navbar cartCount={props.cartItems.length} />
      <Components.SubNavbar />
      <Components.Carousel />
      <Components.Intro />
      <Components.ProductSection productType={productType} />
      <div className="prod-cont">
        <h1>Best Sellers</h1>
        <div className="prod-items">
          {items.map((item, i) => {
            return (
              <Components.Card
                item={item}
                key={item.id}
                isPresent={props.cartItems.find(
                  (cartItem) => item.id === cartItem.id
                )}
                updateCart={(item, action) => handleCartUpdate(item, action)}
              />
            );
          })}
        </div>
        <button>See All Products</button>
      </div>
      <div className="tp-cont">
        <h1>Trusted Partners</h1>
        <div className="tp-items">
          {trustedPartners.map((item, i) => {
            return <img src={item.img} alt={item.altKey} />;
          })}
        </div>
      </div>
      <Components.Footer />
    </div>
  );
}

export default Home;